.signin-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: $background;
  width:100%;
  height:100vh;
  .title{
    h1{
      color:$word;
      font-size: 2rem;
      text-align: center;
      font-weight: bolder;
    }
    p{
      display: none;
    }
  }
  .image{
    width:250px;
    height:250px;
    margin-top:1rem;
  }
  .inputs{
    display: flex;
    flex-direction: column;
    margin-top: 1rem;
    input{
      height:2.5rem;
      width:300px;
      margin-top:2.5rem;
      border-radius: 2rem;
      border:none;
      padding-left:1rem;
      font-size:1rem;
    }
  }
  .link-section{
    margin-top:1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    p{
      font-weight: bold;
      a{
        color:$inner-spot;
      }
    }
  }
}
@media  screen and (min-width:1100px) {
  .signin-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 25% 25% 25% 25%;
    grid-template-areas: 
      "title title . ."
      "img img inputs inputs"
      "img img inputs inputs"
      "img img link link";
    .title{
      display: flex;
      flex-direction: column;
      align-items: center;
      
      grid-area: title;
      h1{
        font-size:3.5rem;
        margin-top: 5rem;
      }
      p{
        display: block;
        margin-top: 1rem;
        font-size:1.8rem;
        color: $word;
      }
    }
    .image{
      grid-area: img;
      width:500px;
      height:500px;
      margin-left:3.8rem;
      font-weight: bold;
    }
    .inputs{
      grid-area: inputs;
      display: flex;
      flex-direction: column;
      align-items: center;
      input{
        width:500px;
        height:60px;
        margin-top:5rem;
        padding-left:3rem;
        font-size:1.8rem;
      }
    }
    .link-section{
      grid-area: link;
      align-self: start;
      p{
        font-size:1.5rem;
      }
    }
  }
}