.register-container {
  background-color: $background;
  width:100%;
  height:100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  .intro{
    .title{
      color:$word;
      font-size:2rem;
      text-align: center;
      font-weight: bolder;
    }
    .description{
      color:$word;
      text-align: center;
      font-size: .8rem;
      font-weight: bold;
      margin-top: 1.2rem;
    }
  }
  .inputs{
    display: flex;
    flex-direction: column;
    input{
      height: 2.5rem;
      width:300px;
      margin-top:2.5rem;
      border-radius: 2rem;
      border:none;
      padding-left: 1rem;
      font-size: 1rem;
    }
  }
  .link-section{
    margin-top:1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    p{
      font-weight: bold;
      a{
        color:$inner-spot;
      }
    }
  }
  .image{
    display: none;
  }
}
@media screen and (min-width:1100px) {
  .register-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 25% 25% 25% 25%;
    grid-template-areas: 
      "intro intro inputs inputs"
      "img img inputs inputs"
      "img img inputs inputs"
      "img img link link";
    .intro{
      grid-area: intro;
      margin-top: 5rem;
      .title{
        font-size: 3.5rem;
      }
      .description{
        font-size: 1.5rem;
      }
    }
    .inputs{
      grid-area:inputs;
      align-items: center;
      input{
        height:60px;
        width:500px;
        margin-top: 5rem;
        padding-left:3rem;
        font-size: 1.8rem;
      }
    }
    .image{
      display: block;
      grid-area: img;
      width:500px;
      height:500px;
      margin-left:3.8rem;
    }
    .link-section{
      grid-area: link;
      align-self: start;
      p{
        font-size: 1.5rem;
      }
    }
  }
}