.index-container{
  background-color: $background;
  width:100%;
  height:100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  .map{
    width:100%;
    height:100vh;
    background-color: gray;
  }
  .search-bar{
    z-index: 999;
    display: flex;
    position: fixed;
    bottom: 20px;
    width: 100%;
    height:70px;
    background-color: $background;
    border:5px $word solid;
    border-radius: 5rem;
    .icon{
      text-align: center ;
      line-height: 60px;
      width:90px;
      background-color: $style;
      border-top-left-radius: 3rem;
      border-bottom-left-radius: 3rem;
    }
    input{
      align-self: end;
      width:300px;
      height:60px;
      border-top-right-radius: 3rem;
      border-bottom-right-radius: 3rem;
      border:none;
      background-color: $background;
      padding-left: 2rem;
      font-size:1.5rem;
      text-decoration:underline;
    }
  }
  .leaflet-container{
    height:100vh
  }
  .hot-chocolate-icon {
    background: white;
    border-radius: 50%;
    box-shadow: 0px 0px 10px black;
  } 

  .hot-chocolate-icon:after {
    display: block;
    width: 30px;
    height: 30px;
    content: "☕";
    font-size: 20px;
  }
}
@media screen and (min-width:1100px) {
  .index-container{
      .search-bar{
        max-width:500px;
        bottom:10%;
        right:8%;
    }
  }
}