.intro-container{
  width: 100%;
  height:100vh;
  background-color: $background;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  .title{
    color:$word;
    font-size: 2rem;
    font-weight: bolder;
  }
  .image{
    margin-top:1rem;
    width:250px;
    height:250px;
  }
  .description{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top:1rem;
    h2{
      color:$word;
      font-weight:bolder;
      font-size:1.5rem;
    }
    p{
      overflow-wrap: break-word;
      font-weight: bolder;
      width:260px;
      text-align: start;
      margin-top: 1rem;
    }
  }
}
@media screen and (min-width:1100px) {
  .intro-container{
    position:relative;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows:25% 25% 25% 25%;
    grid-template-areas: 
      "title title img img"
      "desc desc img img"
      ". . img img"
      ". . button button";
    .title{
      grid-area: title;
      text-align: center;
      font-size: 4rem;
      margin-top:5rem;
    }
    .image{
      grid-area: img;
      width: 500px;
      height: 500px;
    }
    .description{
      overflow: hidden;
      grid-area: desc;
      height:100%;
      margin:0;
      font-size: 1.5rem;
      h2{
        font-size: 2rem;
        width:390px;
        margin-top:1rem;
      }
      p{
        margin-top:1rem;
        width:390px;
      }
    }
   .button {
      position:absolute;
      bottom:150px;
      left: 250px;
      grid-area: button;
      margin:0;
      text-align: center;
      width:250px;
      
    }
  }
}